  pideregistros() {
      let self = this;
      self.loading = true;
      this.regService.getReg('todos','tipounidad3s').then((data) => {
        this.items=data;
        this.items2=data;
        self.loading = false;
  }, (err) => {
    this.msj.showError3('Error',err.error);
   // this.translateService.get('NO_OPERACION').subscribe((value) => { this.msj.showError3('Error',value);  })
  });
  }
  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });
  }
  ionViewDidLoad() {
    this.pideregistros();
  }

  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }


  deleteRecord(item : any, no) : void
  {

    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operación?',
    buttons: [
      {text: 'Si',
        handler: () => {

          let recordID 		: string		= item._id;
          this.regService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'tipounidad3s').then((data) => {
              this.items.splice(no, 1);
              this.items = this.items.concat([]);
              this.items2=this.items
            }, (err) => {
              this.msj.showError3('Error',err.error);
          });

        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();




  }
  value:any;

  updateRecord(item : any,no) : void
   {
    let addModal = this._MODAL .create('Tipounidad3CreatePage', { record : item },{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.items.splice(no, 1);
        this.items = this.items.concat([data]);
        this.items2=this.items
      }
    })
    addModal.present();
    //  this.navCtrl.push('EmpresaCreatePage', { record : item });
   }


